/* eslint-disable */
// fetch.worker.js
// eslint-disable-next-line import/no-anonymous-default-export
export default () => {
  /* eslint-disable-next-line no-restricted-globals */
  self.onmessage = (e) => {
    if (e.data === 'close') {
      self.close();
    } else {
      var url = e.data;

      // hit the API endpoint to get the Data
      var http = new XMLHttpRequest();
      http.open('GET', url);

      http.onreadystatechange = function() {
        if (this.readyState === 4) {
          switch(this.status) {
            case 200:
              postMessage(JSON.parse(http.responseText));
              break;
            default:
              postMessage('Status code: ' + this.status);
              break;
          }
        }
      };

      http.send();
    }
  };
};
