import { Link } from "react-router-dom";
import "./styles.css";

export default function NavBar() {
  return (
    <div className='navBar'>
      <div className='frame'>
        <ul>
          <li><Link className="menu1" to="/">Home</Link></li>
          <li><Link className="menu2" to="/applications">Applications</Link></li>
          <li><Link className="menu3" to="/libraries">Libraries</Link></li>
          <li><Link className="menu4" to="/video-games">Video Games</Link></li>
        </ul>
        <ul className="backlayer">
          <li><Link className="menu1 blue" to="/">Home</Link></li>
          <li><Link className="menu2 red" to="/applications">Applications</Link></li>
          <li><Link className="menu3 blue" to="/libraries">Libraries</Link></li>
          <li><Link className="menu4 red" to="/video-games">Video Games</Link></li>
        </ul>
      </div>
    </div>
  );
};
