import React from "react";
import LibraryCard from '../LibraryCard';
import irisLogo from './Iris.png';
import './styles.css';

export default function Applications() {

    return(
        <div className="mainContent">
            <h2 className="sectionTitle">My Applications</h2>
            
            <div className="gamesContainer">
                <LibraryCard
                          logo={irisLogo}
                          title="Iris"
                          description="Iris is a C++ application that integrates MQTT messaging with a PostgreSQL database. It listens to specified MQTT topics, processes messages, decrypts them if necessary, and stores them in a PostgreSQL database."
                          link="https://github.com/mgrigajtis/iris"
                          github="https://github.com/mgrigajtis/iris"
                        />


            </div>
        </div>
    )
}
