import "./styles.css";

export default function GameCard({ image, title, description, link, extraLink }) {
    return (
      <div className="gameCard">
        <a href={link} target="_blank" rel="noopener noreferrer">
          <img src={image} alt={title} className="gameImage" />
        </a>
        <div className="gameInfo">
          <h3 className="gameTitle">{title}</h3>
          <p className="gameDescription">{description}</p>
          {extraLink && (
            <p className="extraLink">
              <a href={extraLink.url} target="_blank" rel="noopener noreferrer">
                {extraLink.text}
              </a>
            </p>
          )}
        </div>
      </div>
    );
  }