import './styles.css';
import githubLogo from './github.png';

export default function LibraryCard({ logo, title, description, link, github }) {
    return (
      <div className="libraryCard">
        <div className="cardHeader">
          <img src={logo} alt={title} className="libraryLogo" />
          <h3>
            <a href={link} target="_blank" rel="noopener noreferrer">{title}</a>
          </h3>
        </div>
        <p className="libraryDescription">{description}</p>
        <a href={github} target="_blank" rel="noopener noreferrer">
          <img src={githubLogo} alt="GitHub Repo" className="githubIcon" />
        </a>
      </div>
    );
  }