import './styles.css';
import LibraryCard from '../LibraryCard';

import jQueryLogo from './jquery.png';
import javaScriptLogo from './JavaScript.png';

export default function Libraries() {
  return (
    <div className="mainContent">
      <h2 className="sectionTitle">My Open-Source Libraries</h2>
      <div className="librariesContainer">
      <LibraryCard
          logo="https://cdn.cdnlogo.com/logos/r/18/rubygems.svg"
          title="Easystats"
          description="A Ruby Gem that adds statistical functions to an array of numbers. Get standard deviation and more with ease."
          link="https://rubygems.org/gems/easystats"
          github="https://github.com/mgrigajtis/easystats"
        />

        <LibraryCard
          logo="https://cdn.cdnlogo.com/logos/r/18/rubygems.svg"
          title="Easyzpl"
          description="A Ruby library for generating labels on Zebra printers using the ZPL language, simplifying label creation in Rails apps."
          link="https://rubygems.org/gems/easyzpl"
          github="https://github.com/mgrigajtis/easyzpl"
        />

        <LibraryCard
          logo={jQueryLogo}
          title="jQuery Fireworks"
          description="A jQuery plugin that creates animated fireworks effects on an HTML5 canvas."
          link="https://www.npmjs.com/package/jquery-fireworks"
          github="https://github.com/mgrigajtis/jquery-fireworks"
        />

        <LibraryCard
          logo={javaScriptLogo}
          title="Holiday Lights"
          description="A fun animation library that creates falling leaves in Autumn, snow in Winter, and Christmas lights in December."
          link="https://www.npmjs.com/package/holidaylights"
          github="https://github.com/mgrigajtis/HolidayLights"
        />

      </div>
    </div>
  );
};
