import React, { Component } from "react";
import { slide as Menu } from 'react-burger-menu'
import { Link } from "react-router-dom";
import "./styles.css";

export default class HamburgerMenu extends React.Component {
  render () {
    // NOTE: You also need to provide styles, see https://github.com/negomi/react-burger-menu#styling
    return (
      <div className="hamburgerMenuContainer">
      <svg viewBox="0 0 100 80" width="75" height="75">
        <rect width="100" height="20"></rect>
        <rect y="30" width="100" height="20"></rect>
        <rect y="60" width="100" height="20"></rect>
      </svg>
        <Menu>
          <Link className="menu1 menuItem" to="/">Home</Link>
          <Link className="menu2 menuItem" to="/applications">Applications</Link>
          <Link className="menu3 menuItem" to="/libraries">Libraries</Link>
          <Link className="menu4 menuItem" to="/video-games">Video Games</Link>
        </Menu>
      </div>
    );
  }
}
