import ufoBlastorama from './UFOBlastorama.png';
import dropSpider from './DropSpiderLogo.png';
import GameCard from '../GameCard';
import './styles.css';

export default function VideoGames() {
  return (
    <div className="mainContent">
      <h2 className="sectionTitle">My Video Games</h2>
      
      <div className="gamesContainer">
        

        <GameCard
            image={ufoBlastorama}
            title="UFO Blastorama"
            description="My first VR game for the Meta Quest, built with Unity. A VR twist on the classic Space Invaders game."
            link="https://www.oculus.com/experiences/quest/4215799038511978/"
          />

          <GameCard
            image={dropSpider}
            title="Drop Spider"
            description="My first 2D mobile game, created in Godot 3.3 and submitted to the Game Off 2021 Game Jam."
            link="https://oceancucumber.itch.io/drop-spider"
            extraLink={{ text: "Game Off 2021", url: "https://itch.io/jam/game-off-2021" }}
          />

      </div>
    </div>
  );
};
