import React, { Component } from "react";
import WorkerBuilder from './workers/worker-builder';
import Worker from './workers/fetch.worker';
import parse from 'html-react-parser';
import './styles.css';

const fetchInstance = new WorkerBuilder(Worker);

export default class ForSaleTable extends Component {
  constructor(props) {
    super(props);

    this.state = {
      offset: 0,
      pageNumber: 1,
      totalCount: 0,
      tableData: []
    }
  }

  updateCount(newCount) {
    this.setState(state => {
      return {totalCount: newCount}
    });
  }

  getTableData(callback) {
    fetchInstance.onmessage = (message) => {
      if (message) {
        fetchInstance.postMessage('close');

        var tableData = [];

        this.updateCount(message.data.count);

        message.data.properties.forEach(property => {
          tableData.push({
            Address: property.full_address,
            City: property.city.name,
            Price: property.price,
            Currency: property.currency,
            Owner: property.owner
          });
        });

        if (callback) {
          console.log(tableData);
          callback(tableData);
        }
      }
    };

    fetchInstance.postMessage(`https://api.upland.me/properties/list-view?north=39.288213054528455&south=31.289157476123496&east=-109.38714680150149&west=-127.09948312338776&offset=${this.state.offset}&limit=50&sort=asc`);
  }

  updateTableData(data) {
    this.setState(state => {
      return {tableData: data}
    });
  }

  componentDidMount() {
    this.getTableData(data => {
      this.updateTableData(data);
    });
  }

  render() {
    let innerTableCells = '';

    for(let i=0;i<this.state.tableData.length; i++) {
      innerTableCells += `<tr><td>${this.state.tableData[i].Address}</td><td>${this.state.tableData[i].City}</td><td>${this.state.tableData[i].Price}</td><td>${this.state.tableData[i].Currency}</td><td>${this.state.tableData[i].Owner}</td></tr>`;
    };

    return (
      <div className="limiter">
        <div className="containerTable100">
          <div className="wrapTable100">
            <div className="table100">
              <h3>{this.props.name}</h3>
              <div>
                <div className="divTotalCount">
                  <span>Total Properties for Sale: {this.state.totalCount}</span>
                </div>
                <div className="divTablePages">
                  <span>Page {this.state.pageNumber} of {(this.state.totalCount % 50) > 0 ? Math.trunc(this.state.totalCount / 50) + 1 : Math.trunc(this.state.totalCount / 50)}</span>
                </div>
              </div>
              <table>
                <thead>
                  <tr className="table100Head">
                    <th>Address</th>
                    <th>City</th>
                    <th>Price</th>
                    <th>Currency</th>
                    <th>Owner</th>
                    </tr>
                  </thead>
                <tbody>
                  {this.state.totalCount == 0 ? 'loading' : parse(innerTableCells)}
                </tbody>
              </table><br />
              <div className="divTotalCount">
                <span>Total Count: {this.state.totalCount}</span>
              </div>
              <div className="divTablePages">
                <span>Page {this.state.pageNumber} of {(this.state.totalCount % 50) > 0 ? Math.trunc(this.state.totalCount / 50) + 1 : Math.trunc(this.state.totalCount / 50)}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
